import { shuffleArray } from "@rp/common/utils"
import { SuggestedPromptsData } from "./suggestedPrompts.types"

const shufflePrompts = (
  suggestedPrompts: SuggestedPromptsData,
): SuggestedPromptsData["prompts"] => {
  return shuffleArray(suggestedPrompts.prompts).slice(0, 20)
}

export { shufflePrompts }
